<template>
	<div>
		<div style="width: 100%;">
			<v-card class="mb-6">
				<v-card-title class="pb-0">
					<v-text-field
						filled
						placeholder="Judul traction"
						v-model="detailTraction.judul"
						dense
						required
						:rules="requiredRule"
						autocomplete="off"
					></v-text-field>
				</v-card-title>
				<v-card-text>
					<v-textarea
						placeholder="Deskripsi"
						v-model="detailTraction.description"
						rows="2"						
						dense
						required
						:rules="requiredRule"
						autocomplete="off"
					></v-textarea>
				</v-card-text>
			</v-card>
			<v-form v-model="valid" ref="formField">
				<v-card 
					class="mb-6" 
					v-for="(item, i) in detailTraction.pertanyaan" :key="i">
					<v-card-title class="pt-3 pb-0">
						Pertanyaan {{i+1}}
					</v-card-title>
					<v-card-text class="pb-0">
						<v-text-field
							filled
							placeholder="Pertanyaan"
							v-model="item.pertanyaan"
							dense
							required
							:rules="requiredRule"
							autocomplete="off"
						></v-text-field>
					</v-card-text>
					<v-divider></v-divider>
					<v-card-actions>
						<v-spacer></v-spacer>
						<!-- <v-btn 
							icon @click="duplicate(i)"
							color="primary"
							large>
							<v-icon>mdi-content-copy</v-icon>
						</v-btn> -->
						<v-btn 
							icon @click="remove(i)"
							color="red"
							large>
							<v-icon>mdi-trash-can</v-icon>
						</v-btn>
					</v-card-actions>
				</v-card>

				<Notif :msg="msg" :status="status" @visible="visible" />

				<div class="d-flex mb-6">
					<v-spacer></v-spacer>
					<v-btn
						depressed
						:loading="isLoading"
						:disabled="!valid"
						type="submit"
						@click.prevent="validate"
						color="primary">
						simpan
					</v-btn>
				</div>
			</v-form>
		</div>

		<v-btn
      fixed
      bottom
      right
      color="purple"
      dark
      @click="addField"
      fab>
      <v-icon>mdi-plus</v-icon>
    </v-btn>    
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Notif from '@/components/Notif'
	export default{
		components: {
			Notif
		},

		data: () => ({
			required: false,
			valid: true,
			requiredRule: [v => !!v || 'Harus di isi'],
			msg: {
				success: 'Data berhasil di edit',
				error: 'Data gagal di edit',
				visible: false
			}
		}),

		computed: {
			...mapState('dataIncubatorTraction', ['dataTraction', 'isLoading', 'status']),
			detailTraction() {
				let hasil = this.dataTraction
					.filter(traction => traction.id === parseInt(this.$route.params.traction_id))
				hasil = {...hasil[0]}
				return hasil
			}
		},

		methods: {
			addField() {
				this.detailTraction.pertanyaan.push({
					pertanyaan: ''
				})
				// console.log(panjang)
			},
			remove(val) {
				this.detailTraction.pertanyaan.splice(val, 1)
			},
			validate() {
				if (this.$refs.formField.validate()) {
					this.detailTraction.judul_traction = this.detailTraction.judul
					delete this.detailTraction.judul
					delete this.detailTraction.created_at
					delete this.detailTraction.jumlah_jawaban
					this.$store.dispatch('dataIncubatorTraction/putDataTraction', this.detailTraction)
					this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)
					this.msg.visible = true
				}
			},
			visible(val) {
				this.msg.visible = val
				this.$store.commit('dataIncubatorTraction/updateStatus', null)
			}
		}
	}
</script>
